  .navbar{
      position: fixed;
      position: relative;
      transition: top 0.3s;
      background-color: #922c88;
    
    }
  
    .navbar.sticky {
      position: fixed;
      top: 0;
      width: 100%;
      z-index: 100;
  background-color: #fff;
 transition: all 0.4s;
